import { Component, Input, NgZone } from '@angular/core';
import { SharedService } from './shared/shared.service';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  isUserLoggedIn:boolean=false;
  title = 'projectX';
  showSpinner:boolean=false;
  constructor (private zone: NgZone, private router: Router,private sharedService:SharedService) {
    this.sharedService.spinnerChanged.subscribe(res=>{this.showSpinner=res;})
    this.router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
        let islogged=localStorage.getItem('isLoggedIn');
        if (islogged!='true' || event.url=="/" || event.url.indexOf('/login')!=-1 || event.url.indexOf("/welcome")!=-1) {
          this.isUserLoggedIn= false;
        } else {
          this.isUserLoggedIn= true;
        }
      }
    });
  }
  ngOnInit(){
    
  }
}
