import { Component, OnInit } from '@angular/core';
import { MatInputModule } from '@angular/material/input';
import { Router } from '@angular/router';
import { SharedService } from '../shared/shared.service';
import { LoginService } from './login.service';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { AuthService } from '@app/core/service/auth.service';
import { Role } from '@app/shared/enums/role.enum';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  constructor(private router: Router, private fb: FormBuilder, 
    private loginService: LoginService,private authService:AuthService, private sharedService: SharedService) { }
  userName: string = ""
  password: string = ""
  errLabel: string = '';

  ngOnInit(): void {
    this.loginForm = this.fb.group({
      userName: new FormControl({ value: '', disabled: false }, [Validators.required, Validators.minLength(4)]),
      password: new FormControl({ value: '', disabled: false }, [Validators.required, Validators.minLength(4)])

    })
    this.userName = '';
    this.password = '';
    localStorage.removeItem('isLoggedIn');
  }
  onFormSubmit() {
    this.errLabel = ''
    if (this.loginForm.valid) {
      let objLogin = { email: '', password: '' };
      objLogin.email = this.loginForm.get('userName').value;
      objLogin.password = this.loginForm.get('password').value;
      this.loginService.login(objLogin).subscribe(res => {
        if (res.message=="Invalid Credentials") {
          this.errLabel="Invalid username / password"
        }
        else if(res.message=="Employee Login Successful"){
          this.authService.login();
          this.authService.currentUser=res.responsePayload;
          this.authService.currentUser.role=this.getRole(res.responsePayload?.roleId);
          localStorage.setItem('isLoggedIn', 'true');        
          this.router.navigate([this.authService.currentUser.role]);
          
        }
      }, error => {
        this.errLabel = "Something went wrong please try after sometime."
      })
    }
    else{
      this.errLabel="Your username and password didn't match. Please try again."
    }
  }
  getRole(roleId:number){
    if(roleId== Role.Admin || roleId== Role.HR ){
        return Role[Role.Admin].toLowerCase();
      }
      else if(roleId== Role.Employee){
        return Role[Role.User].toLowerCase();
      }
      else{
        return Role[roleId].toLowerCase();
      }
    }  
}
