import { Component, OnInit, Input } from '@angular/core';
import { AuthService } from '../service/auth.service';
import { Role } from '@app/shared/enums/role.enum';

@Component({
  selector: 'app-leftbar',
  templateUrl: './leftbar.component.html',
  styleUrls: ['./leftbar.component.css']
})
export class LeftbarComponent implements OnInit {

  constructor(private authService:AuthService) { }
lstMenus:any[]=[];
  ngOnInit(): void {   
    this.fillNavMenus(this.authService.currentUser.role) 
       
  }
  fillNavMenus(role) {

    switch (role) {
        case Role[Role.Admin].toLowerCase(): {
            this.lstMenus = [
                {
                   routeName: 'Admin Dashboard', iconColor: 'icon-color', routeUrl: 'admin', iconName: 'la la-dashboard', children: [], disabled: false
                },
                {
                    routeName: 'Employees', routeUrl: 'admin/employees', iconColor: 'icon-color', iconName: 'la la-user', children: [], disabled: false
                },

                {
                    routeName: 'Departments', iconColor: 'icon-color', routeUrl: 'admin/departments', iconName: 'la la-building'
                       ,disabled: false
                },
               
                {
                   routeName: 'Designations', routeUrl: 'admin/designations', iconColor: 'icon-color', iconName: 'la la-user-plus'
                   , disabled: false
                },
            ]
         break;
        }
        case  Role[Role.User].toLowerCase():
            {
                this.lstMenus = [
                   
                    {
                        routeName: 'User Dashboard', iconColor: 'icon-color', routeUrl: 'user', iconName: 'la la-dashboard', 
                            disabled: false
                    },
                ]   
                break;           
            }          
        }
    }
}

